instace.interceptors.request.use(config => {
  if(publicUrl.indexOf(config.url)==-1){
    config.headers.common['idpUserId'] = localStorage.getItem('idpUserId')
    config.headers.common['buId'] = localStorage.getItem('idpUserId')
    config.headers.common['client_id'] = localStorage.getItem('clientId')
    config.headers.common['access_token'] = localStorage.getItem('accessToken')
  }
  if(config.baseURL=="https://file/chinadatapay.com"){
    config.headers.common['Content-Type'] ="multipart/form-data" 
  }
  console.log('config==',config)
	return config;
},error=>{
  console.log('00==',error)
  return Promise.reject(error);
});
// 
instace.interceptors.response.use(res => {
	return res;
},error=>{
  console.log('00==',error,res)
  return Promise.reject(error);
});

axios.defaults.withCredentials = true;


function request(url,data={},method='GET',options={}){
  // if(preventFetch) return;
  // preventFetch=true
  let myjson={
    url,
    method,
    data,
  }
  const {baseURL,headers}=options
  console.log('',{baseURL,headers})
  if(baseURL){//baseUrlbaseUrl
    myjson.baseURL=baseURL
  }
  if(headers){//headers
    myjson.headers=headers
  }
  return new Promise((resolve,reject)=>{
    console.log('==myjson=',myjson)
    instace
      .request(myjson)
      .then(res => {
        resolve(res.data);
        console.log('res.data.resultCode===',res.data.resultCode)
        if(res.data.resultCode=='5004'){
          console.log('===',window.location.href.indexOf("test=1"))
          if(window.location.href.indexOf("test=1")<0){
            Vue.prototype.$closeShow()
          }
        }
        //console.log('Vue=',Vue.prototype)
        preventFetch=false
      })
      .catch(error => {
        console.log('',window.location.href.indexOf("test=1"),'error==',error)
        if(window.location.href.indexOf("test=1")<0){
          Vue.prototype.$closeShow('')
        }
        resolve({code:'-111',message:''});
        preventFetch=false
      });
  })
}
//get
function GET(url,options={}){
  return request(url,{},"GET",options)
}
//post
function POST(url,data={},options={}){
  return request(url,data,'POST',options)
}
function PUT(url,data={},options={}){
  return request(url,data,'PUT',options)
}
export{
  GET,
  POST,
  PUT,
}